*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ===== CSS Variables ===== */
:root {
  --font-sans: 'Helvetica Neue', Arial, sans-serif;
  --font-mono: 'Source Code Pro', monospace;
  --color-bg: #f9f9f9;
  --color-text: #333;
  --color-accent: #007acc;
  --max-width: 800px;
}

/* ===== Base Typography ===== */
html {
  font-size: 16px;
  line-height: 1.5;
  font-family: var(--font-sans);
  background-color: var(--color-bg);
  color: var(--color-text);
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

/* ===== Utility Classes ===== */
.container {
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 1rem;
}

.text-center {
  text-align: center;
}

/* ===== Headings ===== */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-sans);
  margin-bottom: 0.5em;
  color: var(--color-accent);
}

/* ===== Links & Buttons ===== */
a {
  color: var(--color-accent);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

button {
  font: inherit;
  cursor: pointer;
  background-color: var(--color-accent);
  color: #fff;
  border: none;
  padding: 0.5em 1em;
  border-radius: 4px;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ===== Forms ===== */
input,
textarea {
  font: inherit;
  padding: 0.5em;
  border: 1px solid #ccc;
  border-radius: 4px;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: var(--color-accent);
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.2);
}

/* ===== Misc ===== */
ul {
  list-style: none;
}

.layout {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.sidebar {
  width: 220px;
  padding: 1rem;
  border-right: 1px solid #ddd;
  overflow-y: auto;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  padding: 0.25rem 0.5rem;
  cursor: pointer;
}

.sidebar li.active {
  font-weight: bold;
  color: var(--color-accent);
}

.main-content {
  flex: 1;
  padding: 1rem 2rem;
  overflow-y: auto;
}

.todo-form {
  display: flex;
  gap: 0.5rem;
  margin: 1rem 0;
  flex-wrap: wrap;
}

.todo-form input {
  flex: 1;
}

.todo-form > input[type='text'],
.todo-form > select,
.todo-form > .todo-due-label input[type='date'] {
  flex: 1 1 150px;
  min-width: 0;
}

.todo-form button {
  flex: 0 0 auto;
}

.todo-form,
.cat-form {
  align-items: center;
}

.cat-form {
  margin-top: 1rem;
  display: flex;
  gap: 0.5rem;
}

.cat-form input {
  width: 100px;
  flex: 1;
  font-weight: 0.9rem;
}

.cat-form button {
  margin-left: 0.5rem;
  font-size: 0.9rem;
}

.todo-list {
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0;
}

.todo-list li {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  margin-bottom: 0.25rem;
  background: #fff;
  border-radius: 4px;
}

.todo-list li:nth-child(odd) {
  background-color: #f0f0f0;
}

.todo-list .todo-text {
  flex: 1;
}

.todo-list li.done {
  text-decoration: line-through;
  opacity: 0.6;
}

.todo-list .todo-due {
  display: inline-block;
  background: #e0f3ff;
  color: #007acc;
  font-size: 0.85rem;
  padding: 0.2em 0.4em;
  border-radius: 4px;
  margin-left: auto;
  margin-right: 0.5rem;
  text-decoration: none;
}

.todo-form .todo-due-label {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: #333;
  margin-left: 0.5rem;
}

.todo-form .todo-due-label input[type='date'] {
  margin-left: 0.25rem;
  padding: 0.25em;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.9rem;
  background: #fff;
}

.todo-list button.toggle,
.todo-list button.remove {
  all: unset;
  cursor: pointer;
  font-size: 1.2rem;
  line-height: 1;
  margin-left: 0.5rem;
}

.todo-list button.toggle {
  color: green;
}

.todo-list button.edit-btn {
  all: unset;
  cursor: pointer;
  margin-left: 0.5rem;
  font-size: 1.1rem;
}

.todo-list button.remove {
  color: var(--color-accent);
}

.todo-list button.note-btn {
  all: unset;
  cursor: pointer;
  margin-left: 0.5rem;
  font-size: 1.2rem;
}

.edit-input {
  flex: 1;
  font: inherit;
  padding: 0.4em;
  margin: 0 0.5rem;
  border: 1px solid var(--color-accent);
  border-radius: 4px;
}

.save-edit-btn {
  margin-left: 0.5rem;
  padding: 0.3em 0.6em;
  font-size: 0.9rem;
  background-color: var(--color-accent);
  color: #fff;
  border-radius: 4px;
}

.note-area {
  margin: 0.5rem 0 0 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.hidden {
  display: none;
}

.note-area .note-text {
  width: 100%;
  font: inherit;
  padding: 0.5em;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.save-note-btn {
  align-self: flex-end;
  font-size: 0.9rem;
  padding: 0.25em 0.5em;
}

.todo-list button.list-btn {
  all: unset;
  cursor: pointer;
  margin-left: 0.5rem;
  font-size: 1.2rem;
}

.list-area {
  margin: 0.5rem 0 0 1.5rem;
  padding-left: 1rem;
  border-left: 2px solid #ddd;
}

.sub-form {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}
.sub-form input {
  flex: 1;
}
.sub-form button {
  flex: 0 0 auto;
}

.sub-list {
  list-style: none;
  padding: 0;
}
.sub-list li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.25rem;
}
.sub-list li.done .sub-text {
  text-decoration: line-through;
  opacity: 0.6;
}

.sub-list button.sub-toggle,
.sub-list button.sub-remove {
  all: unset;
  cursor: pointer;
  font-size: 1.1rem;
  line-height: 1;
}
